.hamburger {
  width: 30px;
  height: 30px;
  position: relative; }

.hamburger__bar {
  padding: 0;
  width: 100%;
  height: 4px;
  background-color: #3fd6bc;
  display: block;
  border-radius: 4px;
  transition: all 0.4s ease-in-out;
  position: absolute; }

.hamburger__bar--bar1 {
  top: 0;
  transform-origin: 5%; }

.hamburger__bar--bar2,
.hamburger__bar--bar3 {
  top: 50%;
  transform: translateY(-50%); }

.hamburger__bar--bar3 {
  right: 0;
  display: none; }

.hamburger__bar--bar4 {
  bottom: 0;
  transform-origin: 5%; }

/* Стили для активной кнопки*/
.js-btn-active .hamburger__bar--bar1 {
  transform: rotate(45deg);
  width: 100%;
  height: 5px; }

.js-btn-active .hamburger__bar--bar3 {
  display: inline;
  transform: rotate(45deg);
  height: 5px;
  background-color: transparent; }

.js-btn-active .hamburger__bar--bar2 {
  transform: rotate(-45deg);
  height: 5px;
  background-color: transparent; }

.js-btn-active .hamburger__bar--bar4 {
  transform: rotate(-45deg);
  height: 5px;
  width: 100%; }

